<template>
<v-container>
  <!-- Tab bar -->
  <v-row class="ma-0 pa-0">
    <v-col class="ma-0 pa-0">
      <template>
        <v-tabs v-model="tab" id="tabBar">
          <v-tabs-slider></v-tabs-slider>
          <v-tab v-for="item in tabItems" :key="item.tab" @click="tabChange(item.tab)">
            {{ item.tab }}
          </v-tab>
        </v-tabs>
      </template>
    </v-col>
  </v-row>
  <!-- Tab bar -->
  <!-- Data Table -->
  <v-row class="ma-0 pa-0">
    <v-col class="ma-0 pa-0">
      <v-data-table :headers="headers" :items="packagings" item-key="id" class="elevation-1" disable-pagination hide-default-footer multi-sort fixed-header height="100%" id="dataTableContent">
        <template v-slot:item="props">
          <tr style="cursor:pointer;" @click="OnPackagingSelect(props.item)">
            <td align="center">
              {{ props.item.name }}
            </td>
            <td align="center">
              {{ props.item.type }}
            </td>
            <td align="center">
              {{ props.item.weight }}
            </td>
            <td align="center">
              {{ props.item.weightUnit }}
            </td>
            <td align="center">
              {{ props.item.width }}
            </td>
            <td align="center">
              {{ props.item.length }}
            </td>
            <td align="center">
              {{ props.item.height }}
            </td>
            <td align="center">
              {{ props.item.dimensionUnit }}
            </td>
            <td align="center">
              {{ props.item.country }}
            </td>
            <td align="center">
              {{ props.item.item }}
            </td>
          </tr>
        </template>
      </v-data-table>
    </v-col>
  </v-row>
  <v-row class="ma-0 pa-0">
    <v-col class="ma-0 pa-0" align="right">
      <v-tooltip top color="#E8D1D9">
        <template v-slot:activator="{ on, attrs }">
          <v-img v-on="on" v-bind="attrs" style="cursor:pointer;top:5px" contain src="@/images/add.png" width="43px" @click="OnPackagingAdd"/>
        </template>
        <span>New</span>
      </v-tooltip>  
    </v-col>
  </v-row>
  <!-- Data Table -->
  <!-- 로딩바 -->
  <v-dialog v-model="loadingDialogShow" persistent width="300">
    <v-card color="primary" dark>
      <v-card-text>
        Please stand by
        <v-progress-linear indeterminate color="white" class="mb-0"></v-progress-linear>
      </v-card-text>
    </v-card>
  </v-dialog>
  <!-- 로딩바 -->

  <!-- 등록 다이얼로그 -->
  <v-dialog v-model="packagingCreateDialogShow" persistent max-width="500px">
    <template v-slot:activator="{ on, attrs }">
      <v-btn color="primary" dark v-bind="attrs" v-on="on" disabled style="display: none"> Open Dialog </v-btn>
    </template>
    <v-card>
      <v-card-text>
        <v-card-title class="ma-0 pa-0">
            <span class="text-h5" style="margin-top: 20px;margin-bottom: 20px;">Packaging Registration</span>
          </v-card-title>
          <v-row>
            <v-col>
              <v-text-field dense hide-details label="Name (*)" placeholder="Name" outlined v-model="createPackaging.name" ref="name"></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-select hide-details :items="typeItems" outlined label="Type (*)" dense v-model="createPackaging.type" ref="type" placeholder="Type"></v-select>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-text-field dense hide-details label="Weight (*)" placeholder="Only number. ex) 300" outlined v-model="createPackaging.weight" ref="weight" oninput="javascript: this.value = this.value.replace(/[^-\.0-9]/g, '' );"></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-select hide-details :items="weightUnitItems" outlined label="Weight Unit (*)" dense v-model="createPackaging.weightUnit" ref="weightUnit" placeholder="Weight Unit"></v-select>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-text-field dense hide-details label="Width (*)" placeholder="Only number. ex) 30" outlined v-model="createPackaging.width" ref="width" oninput="javascript: this.value = this.value.replace(/[^-\.0-9]/g, '' );"></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-text-field dense hide-details label="Length (*)" placeholder="Only number. ex) 30" outlined v-model="createPackaging.length" ref="length" oninput="javascript: this.value = this.value.replace(/[^-\.0-9]/g, '' );"></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-text-field dense hide-details label="Height (*)" placeholder="Only number. ex) 30" outlined v-model="createPackaging.height" ref="height" oninput="javascript: this.value = this.value.replace(/[^-\.0-9]/g, '' );"></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-select hide-details :items="dimenstionUnitItems" outlined label="Dimension Unit (*)" dense v-model="createPackaging.dimensionUnit" ref="dimensionUnit" placeholder="Dimension Unit"></v-select>
            </v-col>
          </v-row>          
          <v-row>
            <v-col style="display: flex">
              <vue-country-code @onSelect="onCreateCountrySelect" v-bind:defaultCountry="KR"></vue-country-code>
                &nbsp;
                <v-text-field v-model="createPackaging.country" dense hide-details label="Country (*)" placeholder="ex) KR / US / JP / CN" outlined ref="destionation" disabled filled></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-text-field dense hide-details label="Item Mapping (*)" placeholder="Item Mapping" outlined v-model="createPackaging.item" ref="itemMapping"></v-text-field>
            </v-col>
          </v-row>
      </v-card-text>            
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="error" outlined @click="OnPackagingCancel"> Cancel </v-btn>
        <v-btn color="primary" @click="OnPackgingCreateOk"> Ok </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
  <!-- 등록 다이얼로그 -->

  <!-- 수정 다이얼로그 -->
  <v-dialog v-model="packagingModifyDialogShow" persistent max-width="500px">
    <template v-slot:activator="{ on, attrs }">
      <v-btn color="primary" dark v-bind="attrs" v-on="on" disabled style="display: none"> Open Dialog </v-btn>
    </template>
    <v-card>
      <v-card-text>
        <v-card-title class="ma-0 pa-0">
            <span class="text-h5" style="margin-top: 20px;margin-bottom: 20px;">Packaging Registration</span>
          </v-card-title>
          <v-row>
            <v-col>
              <v-text-field dense hide-details label="Name (*)" placeholder="Name" outlined v-model="modifyPackaging.name" ref="name"></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-select hide-details :items="typeItems" outlined label="Type (*)" dense v-model="modifyPackaging.type" ref="type" placeholder="Type"></v-select>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-text-field dense hide-details label="Weight (*)" placeholder="Only number. ex) 300" outlined v-model="modifyPackaging.weight" ref="weight" oninput="javascript: this.value = this.value.replace(/[^-\.0-9]/g, '' );"></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-select hide-details :items="weightUnitItems" outlined label="Weight Unit (*)" dense v-model="modifyPackaging.weightUnit" ref="weightUnit" placeholder="Weight Unit"></v-select>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-text-field dense hide-details label="Width (*)" placeholder="Only number. ex) 30" outlined v-model="modifyPackaging.width" ref="width" oninput="javascript: this.value = this.value.replace(/[^-\.0-9]/g, '' );"></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-text-field dense hide-details label="Length (*)" placeholder="Only number. ex) 30" outlined v-model="modifyPackaging.length" ref="length" oninput="javascript: this.value = this.value.replace(/[^-\.0-9]/g, '' );"></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-text-field dense hide-details label="Height (*)" placeholder="Only number. ex) 30" outlined v-model="modifyPackaging.height" ref="height" oninput="javascript: this.value = this.value.replace(/[^-\.0-9]/g, '' );"></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-select hide-details :items="dimenstionUnitItems" outlined label="Dimension Unit (*)" dense v-model="modifyPackaging.dimensionUnit" ref="dimensionUnit" placeholder="Dimension Unit"></v-select>
            </v-col>
          </v-row>          
          <v-row>
            <v-col style="display: flex">
              <vue-country-code @onSelect="onModifyCountrySelect" v-bind:defaultCountry="modifyPackaging.country"></vue-country-code>
                &nbsp;
                <v-text-field v-model="modifyPackaging.country" dense hide-details label="Country (*)" placeholder="ex) KR / US / JP / CN" outlined ref="destionation" disabled filled></v-text-field>
            </v-col>
          </v-row>
          <v-row>
            <v-col>
              <v-text-field dense hide-details label="Item Mapping (*)" placeholder="Item Mapping" outlined v-model="modifyPackaging.item" ref="itemMapping"></v-text-field>
            </v-col>
          </v-row>
      </v-card-text>            
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="error" outlined @click="OnPackagingCancel"> Cancel </v-btn>
        <v-btn color="error" @click="OnPackgingDelete" > Delete </v-btn>
        <v-btn color="primary" @click="OnPackgingModifyOk"> Ok </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
  <!-- 수정 다이얼로그 -->
  </v-container>
</template>
<style scoped>
</style>
<script>
import common from '@/common'
import axios from "axios";
import $ from "jquery";

export default {
    data() {
        return {
            packagingCreateDialogShow: false,
            packagingModifyDialogShow: false,
            loadingDialogShow: false,

            createPackaging: {},
            modifyPackaging: {},
            packagings: [],

            typeItems: ['Box', 'Poly Bag'],
            weightUnitItems: ['kg', 'g'],
            dimenstionUnitItems: ['cm', 'mm'],
            
            tab: 3,
            tabItems: [
                { tab: 'My Details', content: 'Tab 1 Content' },
                { tab: 'Payment', content: 'Tab 2 Content' },
                { tab: 'Shipping', content: 'Tab 3 Content' },
                { tab: 'Packaging', content: 'Tab 4 Content' },
            ],
            headers: [
                { text: "NAME", value: "data1", width: "200", align: "center" },
                { text: "TYPE", value: "data2", width: "150", align: "center" },
                { text: "WEIGHT", value: "data3", width: "210", align: "center" },
                { text: "WEIGHT UNIT", value: "data4", width: "180", align: "center" },
                { text: "WIDTH", value: "data5", width: "140", align: "center" },
                { text: "LENGTH", value: "data6", width: "140", align: "center" },
                { text: "HEIGHT", value: "data7", width: "140", align: "center" },
                { text: "DIMENSION UNIT", value: "data8", width: "200", align: "center" },
                { text: "DESTIONATION", value: "data9", width: "190", align: "center" },
                { text: "ITEM MAPPING", value: "data10", width: "190", align: "center" },
            ],
        };
    },
    components: {
    },
    filters: {       
    },
    computed: {
    },
    methods: {
        OnPackagingAdd() {
            this.createPackaging = {};
            this.packagingCreateDialogShow = true;
        },
        OnPackagingSelect(packaging) {
            this.modifyPackaging = {...packaging};
            this.packagingModifyDialogShow = true;
        },
        tabChange(value) {
            if(value == "My Details") { this.$router.push("/seller-master"); }
            if(value == "Payment") { this.$router.push("/seller-master-payment"); }
            if(value == "Shipping") { this.$router.push("/seller-master-shipping"); }
            //if(value == "Packaging") {this.$router.push("/seller-master-packaging"); }
        },    
        onCreateCountrySelect({ iso2 }) {
            this.createPackaging.country = iso2;
        },
        onModifyCountrySelect({ iso2 }) {
            this.modifyPackaging.country = iso2;
        },
        getPackagings() {
            if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
                this.$alert("Login Fail.", "", "info").then(() => {
                    this.$router.push("/");
                });
                return false;
            }

            this.loadingDialogShow = true;
            
            axios.get(common.makeAPIUrl('/sellers/' + this.$session.get('loginId') + '/packages')
                    , { headers: {'Authorization': this.$session.get('authorization')}}
            )
            .then(response => {
                this.packagings = response.data;
                this.loadingDialogShow = false;
            })
            .catch(err => {
                if(err.response.status == 401) {
                    this.$alert("Login Fail.", "", "info").then(() => {
                        this.$router.push("/");
                    });
                    return false;
                }
                this.$alert(err.response.data.message, '', 'info').then(() => {  })
                this.loadingDialogShow = false;
            })
        },
        OnPackagingCancel() {
            this.packagingCreateDialogShow = false;
            this.packagingModifyDialogShow = false;

            this.createPackaging = {};
            this.modifyPackaging = {};
        },
        OnPackgingCreateOk() { 
            if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
                this.$alert("Login Fail.", "", "info").then(() => {
                    this.$router.push("/");
                });
                return false;
            }

            this.createPackaging.sellerId = this.$session.get('loginId');
            this.loadingDialogShow = true;

            axios.post(common.makeAPIUrl('/sellers/' + this.$session.get('loginId') + '/packages'),
                        this.createPackaging,
                        { headers: {'Authorization': this.$session.get('authorization')}}
            )
            .then(() => {
                    this.loadingDialogShow = false;
                    this.packagingCreateDialogShow = false;
                    this.createPackaging = {};
                    this.getPackagings();
            })
            .catch(err => {
                if(err.response.status == 401) {
                    this.$alert("Login Fail.", "", "info").then(() => {
                        this.$router.push("/");
                    });
                    return false;
                }
                this.loadingDialogShow = false;    
                this.$alert(err.response.data.message, '', 'info').then(() => {  })
                
            })
        },
        OnPackgingModifyOk() {
            if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
                this.$alert("Login Fail.", "", "info").then(() => {
                    this.$router.push("/");
                });
                return false;
            }

            this.modifyPackaging.sellerId = this.$session.get('loginId');
            this.loadingDialogShow = true;
            
            axios.put(common.makeAPIUrl('/sellers/' + this.$session.get('loginId') + '/packages'),
                        this.modifyPackaging,
                        { headers: {'Authorization': this.$session.get('authorization')}}
            )
            .then(() => {
                    this.loadingDialogShow = false;
                    this.packagingModifyDialogShow = false;
                    this.modifyPackaging = {};
                    this.getPackagings();
            })
            .catch(err => {
                if(err.response.status == 401) {
                    this.$alert("Login Fail.", "", "info").then(() => {
                        this.$router.push("/");
                    });
                    return false;
                }
                this.loadingDialogShow = false;    
                this.$alert(err.response.data.message, '', 'info').then(() => {  })
                
            })
        },
        OnPackgingDelete() {
            if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
                this.$alert("Login Fail.", "", "info").then(() => {
                    this.$router.push("/");
                });
                return false;
            }       

            this.loadingDialogShow = true;

            axios.delete(common.makeAPIUrl('/sellers/' + this.$session.get('loginId') + '/packages/' + this.modifyPackaging.id),
                        { headers: {'Authorization': this.$session.get('authorization')}}
            )
            .then(() => {
                this.loadingDialogShow = false;
                this.packagingModifyDialogShow = false;

                this.modifyPackaging = {};

                this.getPackagings();
            })
            .catch(err => {
                if(err.response.status == 401) {
                    this.$alert("Login Fail.", "", "info").then(() => {
                        this.$router.push("/");
                    });
                    return false;
                }
                this.loadingDialogShow = false;    
                this.$alert(err.response.data.message, '', 'info').then(() => {  })
            })
        }
    },
    mounted() {
        if (this.$session.get("loginId") == "" || this.$session.get("authorization") == "") {
          this.$alert(common.messages.login, "", "info").then(() => {
            this.$router.push("/");
          });
          return false;
        }

        $(document).prop("title", "Seller Master");
        $("#bar_header").attr("style", "background-color:#E2E1F1");
        $("#top_header").show();
        $("#topNaviText").text("Seller Master");        

        var dataTableHeight = "height:" + String(window.innerHeight - 240) + "px";
        $("#dataTableContent").children().attr("style", dataTableHeight);  
        
        $("#tabBar>div>div>div>div").next().css({'background-color':'#949494' });
        $("#tabBar>div>div>div>div").next().next().css({'background-color':'#949494' });
        $("#tabBar>div>div>div>div").next().next().next().css({'background-color':'#949494' });
        $("#tabBar>div>div>div>div").next().next().next().next().css({'background-color':'#AF5FAA' });
        $("#tabBar>div>div>div>div").css({'margin-right':'8px' });
        $("#tabBar>div>div>div>div").css({'color':'#FFFFFF' });
        
        this.getPackagings();   
    },
    updated() {
        $(".dropdown-list").css({top:'-205px'})

        $(".v-data-table-header>tr>th").css({'background-color':'#F8DFE8' });
        $(".v-data-table-header>tr>th").css({'text-align':'center' });
        $(".v-data-table-header>tr>th").css({'font-size':'15px' });
        $(".v-data-table-header>tr>th").css({'font-style':'bold' });
    },
};
</script>